h2 {
	@include center(940px);
	padding-top: 25px;
	padding-bottom: 20px;
	
	font-family: "Exo 2", "Open Sans", sans-serif;
	@include font(36px, 500, 42px);
	text-align: center;
}

@media (max-width: $medium) {
	h2 {
		@include center(621px);
	}
}

@media (max-width: $small) {
	h2 {
		@include center(536px);
	}
}

@media (max-width: $extra-small) {
	h2 {
		width: auto;
		margin-left: 20px;
		margin-right: 20px;
	}
}